body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.App {
  text-align: center;
  background-color: #f9f9f9;
  min-width: 690px;
}

.App-header {
  background-color: #282c34;
  height: 8vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
  position:  relative;
}

.logo-right{
  position: absolute;
  top: auto;
  right: 2vh;
}

.logo-right-img{
  height: 1.5em;
  width: auto;
}

.logo-left{
  position: absolute;
  top: auto;
  left: 2vh;
  width: auto;
  height: 1.3em;
}

.main{
  height: 90vh;
}

.alert{
  float: left;
  width: 100%;
}

.main_loading{
  position: absolute;
  top: 50%;
  width: 100%;
  text-align: center;
}

footer{
  background-color: #282c34;
  color: rgb(200, 200, 200);
  height: 2vh;
  font-size: 0.65em;
  clear: both;
  position: relative;
  //margin-top: -2vh;
}

footer p{
  position: absolute;
  top: 50%;
  width: 100%;
  transform: translateY(-55%);
  margin: 0;

}

/* ----- FILTER COLUMN ----- */

.filter-column{
  text-align: left;
  padding-top: 2vh;
  padding-bottom: 2vh;
  height: 90vh;
  overflow-x: hidden;
  overflow-y: scroll;
  padding-right: 0.8vh !important;
  margin-right: 0.1vh;
}

.filter-column h5{
  border-bottom: 1px solid #ededed;
  padding-bottom: 0.7vh;
  margin-bottom: 2vh;
  width: 4.2em;
}

.filter-column .content{
  padding: 1.5vh;
  background-color: white;
  border: 1px solid #c1c1c1;
  border-radius: 5px;
  //height: 89vh;
  -webkit-box-shadow: 6px 6px 10px 0px rgba(189,189,189,0.4);
  -moz-box-shadow: 6px 6px 10px 0px rgba(189,189,189,0.4);
  box-shadow: 6px 6px 10px 0px rgba(189,189,189,0.4);
}

.filter_form{
  font-size: 0.8em;
  //max-width: 100%;
  //height: 93%;
  //overflow-x: hidden;
  //overflow-y: scroll;
  //padding-right: 1vh;
}

.filter-column::-webkit-scrollbar-track
{
	-webkit-box-shadow: inset 0 0 2px rgba(0,0,0,0.9);
	border-radius: 5px;
	background-color: #F5F5F5;
  margin-top:2vh;
  margin-bottom:2vh;
}

.filter-column::-webkit-scrollbar
{
	width: 5px;
}

.filter-column::-webkit-scrollbar-thumb
{
  border-radius: 5px;
	-webkit-box-shadow: inset 0 0 5px rgba(0,0,0,.3);
	background-color: #999;
}

.form-check-input, .form-check-label{
  cursor: pointer;
}

.filterCheckCollapseButton{
  color: #07c;
  font-size: 0.9em;
  cursor: pointer;
  margin: 0.4em 0em;
}

.form-label{
  margin-bottom: 0;
}

.form-label h6{
  margin-top: 1vh;
}
.addInfo{
  font-size: 0.9em;
  font-weight: lighter;
}

.form-label.break{
  margin-bottom: .2vh;
}

.form-label.bottomSpace{
  margin-bottom: 0.6vh;
}

/*
.form-group.row{
  margin-top:1vh;
  //width: 100%;
} */

.smallLabel{
  padding: 0.1vh 1.3vh 0.3vh;

}

.numberFilter label{
  padding-right: 0.5em;
}

.numberFilter div:first-of-type{
  padding: 0;
  display: flex;
  align-items: center
}

.numberFilter div:nth-of-type(2){
  padding-left: 0.5em;
  display: flex;
  align-items: center
}

.rangeFilter{
  padding-left: 0.7vh;
  padding-right: 0.7vh;
}

.filterSlider{
  width: 80%;
  margin: auto;
}

.MuiSlider-markLabel
{
  font-size: 0.9em !important;
  opacity: 0.5;
}

#slider-cost .PrivateValueLabel-circle-33,
#slider-cost .jss33 {
  width: 42px;
  height: 42px;
}

 /* ----- DETAIL COLUMN ----- */

.detail-column{
  /* border-left: 1px solid;
  border-right: 1px solid; */
  height: 90vh;
  overflow-x: hidden;
  overflow-y: scroll;
  padding-right: 0.7vh !important;
  //margin-right: 0.8vh;
  padding-top: 2vh;
  padding-bottom: 2vh;
}

.detail-column::-webkit-scrollbar-track
{
	-webkit-box-shadow: inset 0 0 2px rgba(0,0,0,0.9);
	border-radius: 5px;
	background-color: #F5F5F5;
  margin-top:2vh;
  margin-bottom:2vh;
}

.detail-column::-webkit-scrollbar
{
	width: 5px;
}

.detail-column::-webkit-scrollbar-thumb
{
  border-radius: 5px;
	-webkit-box-shadow: inset 0 0 5px rgba(0,0,0,.3);
	background-color: #999;
}

.detail-column .empty-data{
  margin-top: 2vh;
  padding: 1.5vh;
  height: 90%;
  padding-top: 30%
}

.list_item{
  background-color: white;
  margin-top: 2vh;
  border: 1px solid #c1c1c1;
  border-radius: 5px;
  padding: 1.5vh;
  cursor: pointer;
  -webkit-box-shadow: 6px 6px 10px 0px rgba(189,189,189,0.4);
  -moz-box-shadow: 6px 6px 10px 0px rgba(189,189,189,0.4);
  box-shadow: 6px 6px 10px 0px rgba(189,189,189,0.4);
}
