.list_item_badge div {
  padding: 0;
}

.list_item_badge div:first-child{
  margin: auto;
  line-height: 100%;
}

.list_item_badge div:nth-child(2){
  padding-left: 0.5em;
}

.detail_tab{
  margin-bottom: 2vh !important;
  border-bottom: 1px solid #dee2e6 !important;
}

.detail_tab .nav-link {
  //padding-left: 0.5rem;
  //padding-right: 0.5rem;
  padding: .3rem .5rem;
}

.detail_tab .nav-link:nth-child(2) {
  width: 72%;
}

.tab-content{
  text-align: left;
  cursor: default;
}

.detail_table tr:first-child td {
  border-top: 0 !important;
}

.detail_table.main_info td:first-child {
  width: 40%;
}

.university_link{
  max-width: 2vh;
  overflow-wrap: break-word;
  word-wrap: break-word;
  hyphens: auto;
}

.detail_table.topics td:first-child {
  width: 90%;
}
.detail_table.topics td:nth-child(2) {
  text-align: right;
}

.subjects_chart_no_data {
  text-align: center;
  color: #d4d4d4;
  font-style: italic;
  height: 3em;
  padding-top: 1em;
}

